.login >button{
width: 300px;background-color: #0d7ceb;
color: #eff2f5;border: none;font-size: 30px;
font-weight: 800;border-radius: 10px;

}.login>button:hover{cursor: pointer;}
.p1{
    color: black;font-size: 25px;
}
.h1{
    color: #0d7ceb;font-size: 90px;
}
 
.login>div>img{
    object-fit: contain;
    height: 150px;
}
.login>div{
    display: flex;flex-direction: column;
}

.login{display: grid;
place-items: center;
height: 100vh;
}
