.story{
   position: relative;background-size: cover;width: 120px;height: 200px;
   box-shadow: 0px 5px 17px -7px rbga(0,0,0,0.75);border-radius: 10px;
   transition: 100ms ease-in;cursor: pointer;background-position:center center center center;
   background-repeat: no-repeat;margin-right: 10px; 
  
}
.story:hover{
   transform: scale(1.07);
}
.story_avtar{
   margin: 10px;border:5px solid #2e81f4;
}
.story > h4{
position: absolute;bottom: 5px;left: 20px; color: white;

}
